
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import {mavonEditor} from 'mavon-editor'
import 'mavon-editor/dist/css/index.css'
import axios from 'axios'
export default {
name: 'edit',
props: ['id'],
data: function () {
return {
topic: {
title: '',
content: ''
}
};
},
methods: {

uploadImage: function (pos,file) {
var app = this;



var params = new FormData();
params.append('smfile', file);

axios.post('http://sm.ms/api/upload', params, {



headers: {
'Content-Type': 'multipart/form-data'
}
}).then(function (response) {



app.$refs.md.$img2Url(pos,response.data.data.url);

})

},

edittopic: function () {
var app = this;
this.$http.post('/topics/update', {
topic_id:this.topic.id,
title: this.topic.title,
content: this.topic.content,
accesstoken: this.$store.state.accesstoken,
tab: 'dev'
}).then(function (response) {


app.$router.back();
})
}
},
components: {
Group,
XTextarea,
XButton,
mavonEditor
},
beforeRouteEnter: function (to, from, next) {
next(function (vm) {
vm.$http.get('/topic/' + to.params.id).then(function (response) {
vm.topic = response.data.data;
});
});
},
beforeRouteUpdate: function (to, from, next) {
var app = this;
app.$http.get('/topic/' + to.params.id).then(function (response) {
app.topic = response.data.data;
});
next();
}
}



